// Define column names as a tuple of string literals
const columns = ["name", "ticker", "isin", "cusip", "sedol", "listing"] as const

// Create a type for the column names
type ColumnNames = typeof columns[number]

// Define the type for watchlist items, dynamically generated from columns
type RowData = {
  [K in ColumnNames]: string
}

const csvData: ReadonlyArray<RowData> = [
  {
    name: "Apple Inc.",
    ticker: "AAPL",
    isin: "US0378331005",
    cusip: "37833100",
    sedol: "2046251",
    listing: "XNAS:AAPL",
  },
  { name: "BMW", ticker: "BMW", isin: "", cusip: "", sedol: "72743206", listing: "XETR:BMW" },
  {
    name: "Xiaomi",
    ticker: "1810",
    isin: "KYG9830T1067",
    cusip: "",
    sedol: "",
    listing: "XHKG:1810",
  },
  { name: "SPDR S&P 500 ETF Trust", ticker: "SPY", isin: "", cusip: "", sedol: "", listing: "" },
]

const generateCSV = (): string => {
  const header = columns.join(",")

  const rows = csvData
    .map(row => columns.map(col => row[col as keyof RowData]).join(","))
    .join("\n")

  return `${header}\n${rows}`
}

export { generateCSV, csvData }
